  src: url("GenericMobileSystem.756ae293.ttf");
}

* {
  font-smooth: never;
  --webkit-font-smoothing: none;
}

::selection {
  background: var(--highlight);
  color: var(--highlightedText);
}

body {
  font-family: System, sans-serif;
}

.text-center {
  text-align: center;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  font-family: MS Sans Serif, sans-serif;
}

.font-system {
  font-family: System, sans-serif;
}

.font-sans-serif {
  font-family: MS Sans Serif, sans-serif;
}

